/** @jsx jsx */
import { jsx } from "@emotion/core";
import { FC } from "react";
import WarningMessage from "../../components/MinistereTravail/WarningMessage";

interface SearchResultHeaderText {
  searchResults: any[];
  searchTerms: string;
  loading: boolean;
}

const SearchResultHeaderText: FC<SearchResultHeaderText> = ({
  searchResults,
  searchTerms,
  loading
}) => (
  <WarningMessage>
    {loading
      ? "Chargement en cours ..."
      : searchResults.length > 0 && !loading
      ? `Rsultats de recherche pour ${searchTerms}`
      : `Aucun rsultat ne correspond  la recherche ${searchTerms}`}
  </WarningMessage>
);

export default SearchResultHeaderText;
