/** @jsx jsx */
import { jsx } from "@emotion/core";
import { ReactNode } from "react";
import { RouteComponentProps } from "react-router-dom";

import { AppState, ActionType } from "../../globals";

import calculIndicateurUn from "../../utils/calculsEgaProIndicateurUn";

import Page from "../../components/Page";
import InfoBloc from "../../components/InfoBloc";
import ActionBar from "../../components/ActionBar";
import {
  TextSimulatorLink,
  ButtonSimulatorLink
} from "../../components/SimulatorLink";

import IndicateurUnTypeForm from "./IndicateurUnTypeForm";
import IndicateurUnCsp from "./IndicateurUnCsp/IndicateurUnCsp";
import IndicateurUnCoef from "./IndicateurUnCoef/IndicateurUnCoef";

interface Props extends RouteComponentProps {
  state: AppState;
  dispatch: (action: ActionType) => void;
}

function IndicateurUn({ state, dispatch }: Props) {
  const { csp, coef, autre } = state.indicateurUn;
  const readOnly = state.indicateurUn.formValidated === "Valid";

  // le formulaire d'effectif n'est pas valid
  if (state.effectif.formValidated !== "Valid") {
    return (
      <PageIndicateurUn>
        <InfoBloc
          title="vous devez renseignez vos effectifs avant davoir accs  cet indicateur"
          text={
            <TextSimulatorLink
              to="/effectifs"
              label="renseigner les effectifs"
            />
          }
        />
      </PageIndicateurUn>
    );
  }

  const { effectifsIndicateurCalculable } = calculIndicateurUn(state);

  // les effectifs ne permettent pas de calculer l'indicateur
  if (!effectifsIndicateurCalculable && state.indicateurUn.csp) {
    return (
      <PageIndicateurUn>
        <InfoBloc
          title="Malheureusement votre indicateur nest pas calculable"
          text="car lensemble des groupes valables (cest--dire comptant au
              moins 3 femmes et 3 hommes), reprsentent moins de 40% des
              effectifs."
        />
        <ActionBar>
          <ButtonSimulatorLink
            to={
              state.informations.trancheEffectifs === "50  250"
                ? "/indicateur2et3"
                : "/indicateur2"
            }
            label="suivant"
          />
        </ActionBar>
      </PageIndicateurUn>
    );
  }

  return (
    <PageIndicateurUn>
      <IndicateurUnTypeForm
        csp={csp}
        coef={coef}
        autre={autre}
        readOnly={readOnly}
        dispatch={dispatch}
      />
      {csp ? (
        <IndicateurUnCsp state={state} dispatch={dispatch} />
      ) : (
        <IndicateurUnCoef state={state} dispatch={dispatch} />
      )}
    </PageIndicateurUn>
  );
}

function PageIndicateurUn({ children }: { children: ReactNode }) {
  return (
    <Page
      title="Indicateur cart de rmunration"
      tagline={[
        "Les rmunrations annuelles moyennes des femmes et des hommes doivent tre renseignes par catgorie de postes quivalents (soit par CSP, soit par niveau ou coefficient hirarchique en application de la classification de branche ou dune autre mthode de cotation des postes aprs consultation du CSE) et par tranche dge."
      ]}
    >
      {children}
    </Page>
  );
}

export default IndicateurUn;
