/** @jsx jsx */
import { css, jsx } from "@emotion/core";
import { useState } from "react";
import { RouteComponentProps } from "react-router-dom";

import { ActionType } from "../globals";
import { postIndicatorsDatas } from "../utils/api";

import Page from "../components/Page";
import ButtonAction from "../components/ButtonAction";
import ErrorMessage from "../components/ErrorMessage";
import globalStyles from "../utils/globalStyles";

interface Props extends RouteComponentProps {
  dispatch: (action: ActionType) => void;
}

function Home({ history, location, dispatch }: Props) {
  const [loading, setLoading] = useState(false);
  const [errorMessage, setErrorMessage] = useState(undefined);

  const onClick = () => {
    setLoading(true);
    dispatch({ type: "resetState" });

    postIndicatorsDatas({})
      .then(({ jsonBody: { id } }) => {
        setLoading(false);
        history.push(`/simulateur/${id}`, {
          ...(location.state && location.state),
          openModalEmail: true,
        });
      })
      .catch((error) => {
        setLoading(false);
        const errorMessage =
          (error.jsonBody && error.jsonBody.message) ||
          "Erreur lors de la rcupration du code";
        setErrorMessage(errorMessage);
      });
  };

  if (!loading && errorMessage) {
    return ErrorMessage(errorMessage);
  }

  return (
    <Page
      title="Bienvenue sur Index Egapro"
      tagline={[
        "LIndex d'galit professionnelle a t conu pour faire progresser au sein des entreprises lgalit salariale entre les femmes et les hommes.",
        "Il permet aux entreprises de mesurer, en toute transparence, les carts de rmunration entre les sexes et de mettre en vidence leurs points de progression. Lorsque des disparits salariales sont constates, des mesures de correction doivent tre prises.",
      ]}
    >
      <div css={styles.content}>
        <h2 css={styles.title}>
          Comment calculer et dclarer lindex galit femmes-hommes ?
        </h2>

        <div css={styles.twoColumns}>
          <div css={styles.bloc}>
            <div css={styles.blocImage}>
              <div css={[styles.image, styles.illustrationSimulator]} />
            </div>

            <div css={styles.blocContent}>
              <span css={styles.blocContentStep}>Choix 1</span>
              <span css={styles.blocContentTitle}>
                Calcul et dclaration de l'index
              </span>
              <p css={styles.blocContentBody}>
                Vous pouvez calculer votre index galit professionnelle F/H sur
                Index Egapro et le dclarer, si vous le souhaitez, suite au
                calcul.
              </p>

              <div>
                <ButtonAction
                  onClick={onClick}
                  label="commencer le calcul"
                  disabled={loading}
                  loading={loading}
                />
              </div>
            </div>
          </div>

          <div css={styles.bloc}>
            <div css={styles.blocImage}>
              <div css={[styles.image, styles.illustrationPublish]} />
            </div>

            <div css={styles.blocContent}>
              <span css={styles.blocContentStep}>Choix 2</span>
              <span css={styles.blocContentTitle}>
                Dclaration directe de l'index
              </span>
              <p css={styles.blocContentBody}>
                Vous pouvez dclarer votre index galit professionnelle F/H
                calcul par ailleurs directement via les formulaires suivants :
              </p>
              <ul>
                <li>
                  <a
                    href="https://solen1.enquetes.social.gouv.fr/cgi-bin/HE/SF?P=1162z26z2z-1z-1z91BD660965"
                    target="_blank"
                    rel="noopener noreferrer"
                  >
                    Index 2020 au titre de lanne 2019
                  </a>
                </li>
                <li>
                  <a
                    href="https://solen1.enquetes.social.gouv.fr/cgi-4/HE/SF?P=1162z18z2z-1z-1zFD0365AA36"
                    target="_blank"
                    rel="noopener noreferrer"
                  >
                    Index 2019 au titre de lanne 2018
                  </a>
                </li>
              </ul>
            </div>
          </div>
        </div>
      </div>
    </Page>
  );
}

const styles = {
  content: css({}),
  title: css({
    fontSize: 18,
    lineHeight: "22px",
    fontWeight: "bold",
    marginLeft: 0,
    marginRight: 0,
  }),

  twoColumns: css({
    display: "flex",
    flexDirection: "row",
  }),
  bloc: css({
    marginBottom: 50,
    padding: 10,
    width: "50%",
  }),
  blocImage: css({
    width: 300,
    height: 205,
    flexShrink: 0,
    position: "relative",
  }),

  image: css({
    position: "absolute",
    right: 0,
    top: 0,
    bottom: 0,
    left: -globalStyles.grid.gutterWidth,
    backgroundRepeat: "no-repeat",
    backgroundSize: "contain",
  }),
  illustrationData: css({
    backgroundImage: `url(${process.env.PUBLIC_URL}/illustration-data.svg)`,
  }),
  illustrationSimulator: css({
    backgroundImage: `url(${process.env.PUBLIC_URL}/illustration-simulator.svg)`,
  }),
  illustrationPublish: css({
    backgroundImage: `url(${process.env.PUBLIC_URL}/illustration-publish.svg)`,
  }),

  blocContent: css({
    flexGrow: 1,
    flexShrink: 1,
    flexBasis: 0,
    display: "flex",
    flexDirection: "column",
    paddingTop: 10,
    paddingBottom: 10,
  }),
  blocContentStep: css({
    display: "inline-block",
    fontSize: 12,
    lineHeight: "15px",
    fontWeight: "bold",
    textTransform: "uppercase",
  }),
  blocContentTitle: css({
    display: "inline-block",
    fontSize: 18,
    lineHeight: "22px",
    textTransform: "uppercase",
    marginBottom: 14,
  }),
  blocContentBody: css({
    fontSize: 14,
    lineHeight: "17px",
    marginBottom: "auto",
    paddingBottom: 10,
  }),

  blocContentInfo: css({
    fontSize: 14,
    lineHeight: "17px",
    fontStyle: "italic",
  }),
  button: css({
    display: "inline-flex",
    textDecoration: "none",
  }),
};

export default Home;
